import React, { useImperativeHandle, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { RootState } from '../../../../store/types';
import { updateRequiredDocumentsList } from '../../../../store/common/actions';

const HealthDeclarationForm = React.forwardRef<any, any>((props, ref) => {
  const requiredDocumentsList2 = useSelector((state: RootState) => state.common.requiredDocumentsList);
  const reduxDispatch = useDispatch();
  const getHealthDeclarationFormQA = ['()', '', '', '()',
    '',
    '(1)(14090mmHg)(2)()()(3)(4)GOTGPT(5)(6)(7)(8)(9)(10) (11)',
    '(1)(2)(3)(4)(5)(6)',
    '',
    '',
    '',
    ' ',
    '',
    '(1)(2)(3)(4)(dB)(5)(6)(7)',
    '1~10'];
  const diseaseQuestionnaireList = [
    { title: '', key: 'GeneralDiseaseQuestionnaire' },
    { title: '', key: 'ThyroidDiseaseQuestionnaire' },
    { title: '', key: 'HepatitisOrHepatitisCarrierQuestionnaire' },
    { title: '', key: 'UrinarySystemDiseaseQuestionnaire' },
    { title: '', key: 'EsophagusStomachQuestionnaire' },
    { title: '//', key: 'BronchialDiseaseQuestionnaire' },
    { title: '', key: 'HypertensionQuestionnaire' },
    { title: '//', key: 'HyperuricemiaQuestionnaire' },
    { title: '', key: 'DiabetesQuestionnaire' },
    { title: '', key: 'EpilepsyQuestionnaire' },
    { title: '', key: 'HeartDiseaseQuestionnaire' },
    { title: '', key: 'TumorAndPolypQuestionnaire' },
    { title: '', key: 'PoliomyelitisQuestionnaire' },
    { title: '', key: 'TraumaAndSkeletalInjuryQuestionnaire' },
    { title: '', key: 'EyeDiseaseQuestionnaire' }
  ];

  const [checkboxesState, setCheckboxesState] = useState<boolean[]>(new Array(getHealthDeclarationFormQA.length).fill(false));
  const [checkedDiseaseQuestionnaire, setCheckedDiseaseQuestionnaire] = useState<any[]>([]);

  const handleRadioChange = (index: number, value: string) => {
    setCheckboxesState(prevState => {
      const newState = [...prevState];
      if (value === 'Y') newState[index] = true;
      if (value === 'N') newState[index] = false;
      return newState;
    });
  };

  const handleCheckboxChange = (event: any, index: number) => {
    const { checked } = event.target;
    console.log(diseaseQuestionnaireList[index]);
    const newRequiredDocumentsList = requiredDocumentsList2.map(
      (data: any) => {
        return data.key === diseaseQuestionnaireList[index].key
          ? {
              ...data,
              visible: checked
            }
          : data;
      });
    reduxDispatch(updateRequiredDocumentsList(
      {
        requiredDocumentsList: newRequiredDocumentsList
      }
    ));
    if (checked) {
      setCheckedDiseaseQuestionnaire([
        diseaseQuestionnaireList[index],
        ...checkedDiseaseQuestionnaire
      ]);
    } else {
      setCheckedDiseaseQuestionnaire((prevState) => {
        const newList = [...prevState];
        newList.splice(newList.indexOf(diseaseQuestionnaireList[index]), 1);
        return newList;
      });
    }
  };

  const getDiseaseQuestionnaireListBody = () => {
    return (
      diseaseQuestionnaireList.map((data, index) => {
        return (
          index % 2 === 0 && (
            <div
              style={{ border: '1px solid #348d8f', borderTop: 'none' }}
              key={`qa-${index}`} className="row form-row m-0 p-2"
            >
              <div className="row m-0">
                <div className="d-flex form-check col-6 align-items-center">
                  <input
                    title=""
                    type="checkbox"
                    className="form-check-input me-2 mb-1"
                    value="Y"
                    style={{ marginTop: '0px' }}
                    onChange={(event) => handleCheckboxChange(event, index)}
                  />
                  <label
                    className="form-check-label me-2 text-nowrap labelName"
                  >{data.title}
                  </label>
                </div>
                {
                                    diseaseQuestionnaireList[index + 1] && (
                                      <>
                                        <div className="d-flex form-check col-6 align-items-center">
                                          <input
                                            title=""
                                            type="checkbox"
                                            className="form-check-input me-2 mb-1"
                                            value="Y"
                                            style={{ marginTop: '0px' }}
                                            onChange={(event) => handleCheckboxChange(event, index + 1)}
                                          />
                                          <label
                                            className="form-check-label me-2 text-nowrap labelName"
                                          >
                                            {diseaseQuestionnaireList[index + 1].title}
                                          </label>
                                        </div>
                                      </>
                                    )
                                }
              </div>
            </div>
          )
        );
      })
    );
  };

  useImperativeHandle(ref, () => ({
    getCheckedDiseaseQuestionnaire: () => {
      return checkedDiseaseQuestionnaire;
    }
  }));

  return (
    <>
      <div className="row m-0 p-4" style={{ border: '1px solid #348d8f' }}>
        <div className="col-6">
          <div className="name labelName">()</div>
          <input type="labelName" name="rocId" className="form-control" />
        </div>
        <div className="col-6">
          <div className="name labelName">()</div>
          <input type="labelName" name="rocId" className="form-control" />
        </div>
      </div>
      {
              getHealthDeclarationFormQA.map((data, index) => {
                return (
                  <div
                    style={{ border: '1px solid #348d8f', borderTop: 'none' }}
                    key={`qa-${index}`} className="row form-row m-0 p-2"
                  >
                    <div className="col-12">
                      <div className="labelName">{data}</div>
                      <div className="radios-wrapper">
                        <div className="d-flex py-1 align-items-center">
                          <input
                            title=""
                            type="radio"
                            className="form-check-input me-2 mb-1"
                            value="Y"
                            style={{ marginTop: '0px' }}
                            onChange={() => handleRadioChange(index, 'Y')}
                            checked={checkboxesState[index]}
                          />
                          <label
                            className="form-check-label me-2 text-nowrap labelName"
                          >
                          </label>
                        </div>
                        <div className="d-flex py-1 align-items-center">
                          <input
                            title=""
                            type="radio"
                            className="form-check-input me-2 mb-1"
                            value="N"
                            style={{ marginTop: '0px' }}
                            onChange={() => handleRadioChange(index, 'N')}
                            checked={!checkboxesState[index]}
                          />
                          <label
                            className="form-check-label me-2 text-nowrap labelName"
                          >
                          </label>
                        </div>
                      </div>
                    </div>
                  </div>
                );
              })
          }
      {
              checkboxesState.filter(data => data).length > 0 && (
                <>
                  <div
                    className="row m-0 p-2"
                    style={{ border: '1px solid #348d8f', background: '#edf5f4', borderTop: 'none' }}
                  >
                    <div className="col-12">
                      <div className="name labelName"></div>
                    </div>
                  </div>
                  <div
                    className="row m-0 p-2"
                    style={{ border: '1px solid #348d8f', background: '#edf5f4', borderTop: 'none' }}
                  >
                    <div className="col-12">
                      <div className="name labelName">()</div>
                    </div>
                  </div>
                  {
                    getDiseaseQuestionnaireListBody()
                  }
                  <div
                    className="row m-0 p-2"
                    style={{ border: '1px solid #348d8f', background: '#edf5f4', borderTop: 'none' }}
                  >
                    <div className="col-12">
                      <div className="name labelName"></div>
                    </div>
                  </div>
                  <div
                    className="row m-0 p-2"
                    style={{ border: '1px solid #348d8f', background: '#edf5f4', borderTop: 'none' }}
                  >
                    <div className="col-12">
                      <div
                        className="name labelName"
                      >
                      </div>
                    </div>
                  </div>
                  <input type="labelName" name="healthConditionDisclosure" className="form-control" />

                </>
              )
      }

    </>
  );
});

HealthDeclarationForm.displayName = 'HealthDeclarationForm';
export default HealthDeclarationForm;
